type ProgressBarProps = {
  currentStep: number
  steps: { title: string; description: string }[]
}

export default function ProgressBar({ currentStep, steps }: ProgressBarProps) {
  return (
    <div className="w-full py-4 mb-8">
      <div className="flex justify-between">
        {steps.map((step, index) => (
          <div key={index} className="flex flex-col items-center w-1/4">
            <div className={`w-8 h-8 rounded-full flex items-center justify-center mb-2 
              ${index + 1 <= currentStep ? 'bg-blue-600 text-white' : 'bg-gray-200 text-gray-600'}`}>
              {index + 1}
            </div>
            <div className="text-center">
              <div className={`text-sm font-medium ${index + 1 <= currentStep ? 'text-blue-600' : 'text-gray-500'}`}>
                {step.title}
              </div>
              <div className="text-xs text-gray-500">
                {step.description}
              </div>
            </div>
          </div>
        ))}
      </div>
      <div className="relative mt-2">
        <div className="absolute top-1/2 left-0 right-0 h-0.5 bg-gray-200" />
        <div 
          className="absolute top-1/2 left-0 h-0.5 bg-blue-600 transition-all duration-300" 
          style={{ width: `${(currentStep - 1) * 33.33}%` }}
        />
      </div>
    </div>
  )
} 